	module.exports = {"data":{"markdownRemark":{"html":"<h2>Color Theory</h2>\n<p>Color Theory is a practical application of mixing colors and using color to achieve a specific visual effect.</p>\n<p>Colors are used in all sorts of ways.\nThe choice of colors in visual display is often constrained by hardware, software running on the hardware, limitations of the human eye and variations on how humans perceive color.\nLimits to choices are also shaped by artificial constraints supplied by a project, such as having to conform to design style and branding.</p>\n<p>Using color theory can help meet the requirements of projects while maximizing legibility of text and accessibility of content to viewers with different viewing traits or needs.</p>\n<h3>Color Wheel or Color Picker</h3>\n<p>A color wheel or picker is a tool used by designers to specify or locate color.\nColor pickers may include a color spectrum or they may be organized by defined categories of light, such as brightness and hue.</p>\n<p><strong>Primary Colors</strong></p>\n<p>In art and illustration, colors are traditionally separated into three categories: Primary Colors, Secondary Colors, and Tertiary Colors.\nPrimary colors are colors that cannot be mixed or formed by any combination of other colors.\nSecondary colors are the ones formed by mixing two of the primary colors.\nTertiary colors are the ones formed by mixing all three primary colors.</p>\n<p>In traditional painting, the colors are red, yellow and blue (RYB).\nAll other colors are derived from these three.\nThe actual hue of each primary color affects the range of colors available.</p>\n<p>Computer visual displays commonly use a different set of three primary colors to approximate color: red, green and blue (RGB).\nEvery color displayed is made of varying the brightness of the primary colors.</p>\n<p>Computer printers commonly use four primary colors to represent color.\nA black is sometimes used as a contrast medium to increase the range of contrast.\nThe combination of Primary colors Cyan, Magenta, Yellow, and Key (CMYK) is common. “Key” represents black.\nKey is a traditional term for the most significant impression of a combination of imprints.</p>\n<h3>Web colors</h3>\n<p>Web standards including HTML, CSS, and graphics file formats: jpeg, PNG, GIF, and SVG use primary colors of red, green, and blue.\nEach technology or device displays a slightly different hue of primary color.\nFor example, a pure red on one monitor may look more pink or orange on another.\nThese variances add to the complexity of attempting to portray accurate colors on a computer screen.\nThis is a significant issue, for example, when trying to:</p>\n<ul>\n<li>represent a product with a specific color hue, or</li>\n<li>represent a rainbow of color, or</li>\n<li>alert a user with messages having various degrees of importance.</li>\n</ul>\n<p>Web design, advertising, and human-computer interface heuristics also recognize that colors carry cultural connotations and implications. </p>\n<p><a href='https://github.com/freecodecamp/guides/blob/master/README.md' target='_blank' rel='nofollow'>This quick style guide will help ensure your pull request gets accepted</a>.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://en.wikipedia.org/wiki/Spectral_color#Table_of_spectral_or_near-spectral_colors\">Table of spectral or near-spectral colors in RGB</a></p>\n<p><a href=\"/visual-design/color-theory/usability/index.md\">Color and Usability</a>\n<a href=\"/visual-design/color-theory/color-and-culture/index.md\">Color and Culture</a></p>","fields":{"slug":"/visual-design/color-theory/"},"frontmatter":{"title":"Color Theory"}}},"pathContext":{"slug":"/visual-design/color-theory/"}}

